body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  background-image: linear-gradient(to left, black, gray);
}

.navbar h2 {
  color: whitesmoke;
}

.navbar .items {
  vertical-align: center;
  padding-left: 450px;
  padding-top: 10px;
}

.navbar li {
  list-style: none;
  display: inline;
  padding-left: 10px;
  text-align: left;
}

.navbar li a {
  color: lightgray;
}

.navbar li a:hover {
  color: whitesmoke;
  text-decoration: none;
}

.navbar li .active {
  color: white;
}

.cart {
  border-top: 2px solid whitesmoke;
  padding: 5px 0px;
  background-image: linear-gradient(to top left, rgb(44, 37, 37), gray);
}
.cart .priceCard {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.cart .priceCard .card {
  margin: 5px;
  padding: 6px;
}

.cart .priceCard .card img {
  height: 200px;
  width: 100%;
  padding-bottom: 5px;
  border-radius: none;
}

.cart .priceCard .card h4,
h5 {
  text-align: center;
}

.cart .priceCard .card .details {
  height: 120px;
  padding-left: 5px;
}

.cart .priceCard .card .rating b {
  color: rgb(223, 196, 64);
  stroke: 2px black;
}

.cart .priceCard .card .rating span {
  color: rgb(223, 196, 64);
}

.cart .summary h3 {
  color: white;
  -webkit-text-stroke: 1px darkgray;
}

.cart .summary .items {
  background-color: whitesmoke;
  border-radius: 5px;
  padding: 5px;
}

.cart .summary .items button {
  padding: 0px 5px;
  padding-top: 0px;
  position: absolute;
  right: 25px;
}

.cart .priceCard button:disabled {
  background-color: gray;
  border: gray;
}

.cart .summary .checkout {
  margin-top: 10px;
  margin-left: 40px;
}

.footer {
  border-top: 2px solid whitesmoke;
  background-image: linear-gradient(to left, rgb(107, 105, 105), black);
  padding: 5px;
  color: white;
}

.footer .links {
  padding-left: 680px;
}

.footer p {
  margin-bottom: 0px;
}
